<template>
<div id="app" v-if="ready" class=" bg-grey">
	
	<div class="container py-5 ">	
		<div class="row ">	
				
				<div class="col-lg-3 mb-5" id="search_box">
					<div class="mt-0">	
						<div class="form-group">	
							<div class="row justify-content-between">
										<div class="col text-left pr-0"><label for="formControlRange">Ship from</label></div>
										<div class="col text-right  pl-0"><span id="origin_loc" class="loc_name"></span></div>
							</div>
							<div class="input-group input-group-sm">
										<div class="input-group-prepend">
										  <div class="input-group-text"><span  v-bind:class="'flag-icon flag-icon-'+this.ori_cn"></span></div>
										</div>									
										<input type="text" class="form-control" id="f_o" placeholder="Place or Postal code" v-model="origin" v-on:keyup="set_origin()">
							 </div>
						</div>
					</div>
					<div class="">						
						<div class="form-group">
							<div class="row justify-content-between">
										<div class="col text-left pr-0"><label for="formControlRange">Destination</label></div>
										<div class="col text-right pl-0"><span id="dest_loc" class="loc_name"></span></div>
							</div>
							<div class="input-group input-group-sm">
										<div class="input-group-prepend">
										  <div class="input-group-text"><span  v-bind:class="'flag-icon flag-icon-'+this.des_cn"></span></div>
										</div>									
										<input type="text" class="form-control" id="f_d" placeholder="Place or Postal code" v-model="destination" v-on:keyup="set_dest()">
							</div>
						</div>
					</div>
					<div class="">
						<label for="formControlRange">Weight</label>
						<div class="input-group input-group-sm">
									<input type="text" class="form-control" id="f_w" placeholder="1.5" v-model="weight">									
									<div class="input-group-append">
									  <div class="input-group-text">lbs</div>
									</div>
								 </div>
					</div>
					<div class="mt-3">						
						<div class="row justify-content-between">	
								<div class="col">
									<label  class="mb-0">Dimension</label>
								</div>
								<div class="col text-right">
									<div class="row">
									<div class="col">
									<input class="form-check-input" type="radio" id="unit_inch" name="unit" value="inch" v-model="unit">
									<label class="form-check-label" for="unit_inch">inch</label>
									</div>
									<div class="col">									
									<input class="form-check-input" type="radio" id="unit_cm" name="unit" value="cm" v-model="unit">
									<label class="form-check-label" for="unit_cm">cm</label>
									</div>
									</div>
								</div>
						</div>
						<div class="form-group row ">
							<div class="col-sm-4">
								<div class="input-group-sm mt-2">
									<input type="text" class="form-control" id="f_pl" placeholder="Length" v-model="pl">		
								 </div>
							</div>
							<div class="col-sm-4">
								<div class="input-group-sm mt-2">
									<input type="text" class="form-control" id="f_pw" placeholder="Width" v-model="pw">		
								 </div>
							</div>							
							<div class="col-sm-4">
								<div class="input-group-sm mt-2">
									<input type="text" class="form-control" id="f_ph" placeholder="Height" v-model="ph">		
								 </div>
							</div>
						</div>
					</div>
					<div class="">
						<div class="mt-3 text-right">
							<button class="btn btn-success" type="submit" v-on:click="calc()">Calculate</button>
						</div>
					</div>
			</div>
	
			<div class="col-lg-9">
				<template v-if="busy">
					Loading...
				</template>
				<template v-else>
					<div class="row pl-0 justify-content-between">
						<div class="col">
							<h5 class="mb-0">Results</h5>
							Showing {{ data.results.length }} results
						</div>
						<div class="col">
							<div class="input-group justify-content-end">
							  <div class="input-group-prepend" id="button-addon3">
								<button v-bind:class="'btn '+currentSort(0)" type="button" v-on:click="sort=0">Cheap</button>
							  </div>
							  <div class="input-group-prepend" id="button-addon3">
								<button v-bind:class="'btn '+currentSort(1)" type="button" v-on:click="sort=1">Fastest</button>
							  </div>
							</div>
						</div>
					</div>
					<div class="row pl-0 pt-3 pb-5">
						<div class="col-lg-4 mb-5" v-for="d in data.results">
									<div data-animate-hover="1" data-toggle="hidden">
										<div class="card shadow animate-this text-truncateh ">
											<div class="pt-5 pb-4 text-center">
												<img alt="" v-bind:src="'/static/images/'+d.carrier+'.jpg'" class="img-saturate" style="width: 50px;">
											</div>
											<div class="px-4 text-center">
												<span class="btn btn-sm btn-light btn-outline-primary btn-circle"><b>{{ d.cost }}</b></span>
												<p class="mt-0" style="font-size:13px;">
													<ul class="list-group list-group-flush">
														  <li class="list-group-item"><b>{{ d.service }}</b></li>
														  <li class="list-group-item"><i>Delivery speed: {{ d.delivery.days }}</i></li>
														  <li class="list-group-item">For {{ data.weight-1 }} lbs - {{ d.compare }}</li>
													</ul>
												</p>
											</div>
											<div class="bg-warning p-2 text-center text-white">
												<a v-bind:href="'#/view/'+d.carrier+'/'+data.origin+'/'+data.coords">LOCATION</a>
											</div>
										</div>
									</div>
						</div>
				
					</div>
				</template>
			</div>
		</div>
	</div>
</div>
<div id="busy" v-else>
		<busy-screen></busy-screen>
</div>
</template>

<script>
import axios from 'axios';
import WinBusy from '@/components/Busy'

export default {
  name: 'Home',
  components:{
	'busy-screen':WinBusy
  },
  props:['sign'],
  data () {
    return {
		ready: false,
		busy: true,
		url: this.$parent.url,
		data: [],
		sort: 0,
		o_place: '',
		d_place: '',
		origin: '',
		coords: '',
		ori_zip: '',
		des_zip: '',
		ori_cn: '',
		des_cn: '',
		destination: '',
		unit: '',
		weight: '',
		pl:'',
		ph:'',
		pw:''
	}
  },
  watch:{
	'sort'(to,from){
		this.results();
	}
  },
  created: function() {
	this.$parent.current('calulate');
	
	this.o_place=localStorage.origin;
	this.d_place=localStorage.destination;
	this.origin=localStorage.origin;
	this.destination=localStorage.destination;
	this.weight=localStorage.weight;
	this.unit=localStorage.unit;
	
	this.coords=localStorage.coords;
	this.ori_zip=localStorage.ori_zip;
	this.des_zip=localStorage.des_zip;
	
	this.pl=localStorage.pl;
	this.ph=localStorage.ph;
	this.pw=localStorage.pw;
  },
  mounted: function() {
	this.ready=true;
	
	this.ori_cn=this.origin.split(',')[2].toLowerCase().replace(' ','');
	this.des_cn=this.destination.split(',')[2].toLowerCase().replace(' ','');
	
	this.results();
  },
  methods: {		
		currentSort: function(id){
			if(this.sort==id)
				return 'btn-primary';
			else
				return 'btn-outline-secondary';
		},
		set_origin: function(){
			var to=this.origin;
			if(to.length>4){
			axios.get('https://ezcmd.com/apps/api_geo_postal_codes/search/832b62f1efe4928b85b74238562b4929/389?zip_query='+to)
			.then(r =>{ 
				if(r.data.success){
					this.ori_cn=r.data.search_results[0].country_code.toLowerCase();
					this.coords=r.data.search_results[0].coords;
					this.ori_zip=r.data.search_results[0].postal_code;
					this.o_place=r.data.search_results[0].place_name_string+', '+r.data.search_results[0].state+', '+r.data.search_results[0].country_code;
					$('#origin_loc').text(this.o_place);
					
				} else{
					$('#origin_loc').text('Invalid postal code');
				}
			}).catch(e =>{
			
			});
			}
		},		
		set_dest: function(){
			var to=this.destination;
			if(to.length>4){
			axios.get('https://ezcmd.com/apps/api_geo_postal_codes/search/832b62f1efe4928b85b74238562b4929/389?zip_query='+to)
			.then(r =>{ 
				if(r.data.success){
					this.des_cn=r.data.search_results[0].country_code.toLowerCase();
					this.des_zip=r.data.search_results[0].postal_code;
					this.d_place=r.data.search_results[0].place_name_string+', '+r.data.search_results[0].state+', '+r.data.search_results[0].country_code;
					$('#dest_loc').text(this.d_place);
				} else{
					$('#dest_loc').text('Invalid postal code');
				}
			}).catch(e =>{
			
			});
			}
		},
		calc: function(){				
			if($('#origin_loc').text()!=''){ localStorage.origin=$('#origin_loc').text(); }
			if($('#dest_loc').text()!=''){ localStorage.destination=$('#dest_loc').text(); }
			localStorage.ori_zip=this.ori_zip;
			localStorage.des_zip=this.des_zip;
			
			localStorage.weight=this.weight;
			localStorage.unit=this.unit;
			this.results();
		},
		results: function(){	
			var err=0;
			$('#search_box input').removeClass('is-invalid');
			if(this.pl<1){
				$('input#f_pl').addClass('is-invalid');
				err++;
			}
			if(this.ph<1){
				$('input#f_ph').addClass('is-invalid');
				err++;
			}
			if(this.pw<1){
				$('input#f_pw').addClass('is-invalid');
				err++;
			}
			if(this.weight<1){
				$('input#f_w').addClass('is-invalid');
				err++;
			}
			if(err==0){
			
				this.busy=true;
				this.data=[];
				axios.get(this.url+'/calculate?o='+this.ori_zip+'&d='+this.des_zip+'&orig='+this.o_place+'&dest='+this.d_place+'&c='+this.coords+'&w='+this.weight+'&pl='+this.pl+'&ph='+this.ph+'&pw='+this.pw+'&s='+this.sort)
				.then(r =>{ 
					this.busy=false;
					if(r.data.success){
						this.data=r.data;
					} else{
						
					}
				}).catch(e =>{
					this.busy=false;
				});
			}
		}
	}
}


</script>

<style scoped>
.section-rotate{overflow-x:hidden;  position: absolute;
    z-index: -2;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;}
	
.section-rotate .section-inner {
  
}
.section-rotate .section-inner {
	  position: relative;
    top: -10%;
    left: -4rem;
    width: 130%;
    height: 80%;
    transform: rotate(-4deg);
    border-bottom-left-radius: 3rem;
}
@media (min-width: 992px)
.section-rotate .section-inner {
    overflow: hidden;
    background: inherit;
}
.bg-gradient-primary {
    background: linear-gradient(80deg,#510fa8 0,#320fa8 100%)!important;
}
</style>



