import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/components/Home'
import Calculate from '@/components/Calculate'
import View from '@/components/View'
Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Home',
      component: Home
    },
	{
      path: '/calculate',
      name: 'Calculate',
      component: Calculate
    },
	{
      path: '/view/:com/:pin/:coords',
      name: 'View',
      component: View,
	  props:true
    }
  ]
})



