<template>
<div id="app" v-if="ready" class=" bg-grey">
	
	<div class="container py-5 ">	
		<div class="row ">	
				
	
			<div class="col-lg-12">
				
				<div class="row pl-0 justify-content-between">
					<div class="col">
						<h3>Nearest Locations</h3>
					</div>
				</div>
				<div class="row mt-3">
				<div class="col-sm-4 mb-5" v-for="d in data.value">					
					<div class="card">
					  <div class="card-body">
						<h5 class="card-title">{{ d.name }}</h5>
						<p class="card-text">{{ d.telephone }}</p>
						<p class="card-text">
							{{ d.address.neighborhood }},
							{{ d.address.addressLocality }},
							{{ d.address.addressRegion }},
							{{ d.address.postalCode }},
						</p>
						<a v-bind:href="d.webSearchUrl" target="_BLANK" class="btn btn-outline-primary">Direction</a>
					  </div>
					</div>
				</div>
				</div>
			</div>
		</div>
	</div>
</div>
<div id="busy" v-else>
		<busy-screen></busy-screen>
</div>
</template>

<script>
import axios from 'axios';
import WinBusy from '@/components/Busy'

export default {
  name: 'ViewLocation',
  components:{
	'busy-screen':WinBusy
  },
  props:['coords','pin','com'],
  data () {
    return {
		ready: false,
		busy: true,
		url: this.$parent.url,
		data: []
	}
  },
  watch:{
	'coords'(to,from){
	
	}
  },
  created: function() {
	this.$parent.current('location');
  },
  mounted: function() {
	this.ready=true;
	this.calc();
  },
  methods: {		
		calc: function(){	
			this.busy=true;
			this.data=[];
			var c=this.coords.split(','); // '+this.pin+'
			axios.get('https://api.cognitive.microsoft.com/bing/v7.0/entities?q='+this.com+' near me&mkt=en-us&count=10&offset=0&safesearch=moderate', 
			{
				headers: {
				  'Ocp-Apim-Subscription-Key' : '28f30cd6ce5e480aad61c68548dcf82a',
				  'X-Search-Location' : 'lat:'+c[0]+';long:'+c[1]+';re:5000'
				} 
			})
			.then(r =>{ 
				this.busy=false;
				this.data=r.data.places;
			}).catch(e =>{
				this.busy=false;
			});
		}
	}
}


</script>

<style scoped>
.section-rotate{overflow-x:hidden;  position: absolute;
    z-index: -2;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;}
	
.section-rotate .section-inner {
  
}
.section-rotate .section-inner {
	  position: relative;
    top: -10%;
    left: -4rem;
    width: 130%;
    height: 80%;
    transform: rotate(-4deg);
    border-bottom-left-radius: 3rem;
}
@media (min-width: 992px)
.section-rotate .section-inner {
    overflow: hidden;
    background: inherit;
}
.bg-gradient-primary {
    background: linear-gradient(80deg,#510fa8 0,#320fa8 100%)!important;
}
</style>



