<template>
	<div class="body  mT-20" id="app">
		<nav class="navbar sticky-top navbar-expand-lg navbar-dark bg-dark">
			<div class="container  align-items-center">				
				<a class="navbar-brand" href="#/"><img src="/static/images/logo.png"  height="50" class="d-inline-block align-top" alt=""></a>
				<a class="navbar-brand" href="#/">Slug Surf</a>
				<div class="collapse navbar-collapse "  id="navbarTogglerDemo01">
					<ul class="navbar-nav mr-auto mt-2 mt-lg-0 hide">
					  <li class="nav-item active">
						<a class="nav-link" href="#">Products</a>
					  </li>
					  <li class="nav-item">
						<a class="nav-link" href="#">Pricing</a>
					  </li>
					</ul>
					<form class="form-inline my-2 my-lg-0 hide">
					
						<a class="nav-link" href="#">Sign in</a>
					  <button class="btn btn-outline-dark bg-light   my-2 my-sm-0" type="submit">Sign Up</button>
					</form>
				</div>
			</div>
		</nav>			
		<section id="content" class="restrict">
			<router-view :sign="sign" />
		</section>
  </div>
</template>

<script>
export default {
  name: 'App',
  data () {
    return {
		url:'https://shipnow.azurewebsites.net',
		surl:'http://localhost:3000',
		ready: false,
		sign: false,
		menu: '',
	}
  },
  mounted: function() {
	this.ready=true;
  },
  updated: function(){

  },
  methods:{
	current: function(e){
		this.menu=e;		
	},
	panic: function(e){
		$('#hungry').show();
	}

  }
}

</script>

<style>
.hide{display:none;}
.bg-grey{ background:#f3f3f3;}
.line{border-top:1px solid #e1e1e1;}
.loc_name{text-align:right;font-style: italic;}
</style>



